<template>
  <div v-if="timeTitle">
    <div v-if="tableType === 'rep_9'">
      {{ parsedTitleName }} на
      <span class="page-subtitle-bold">{{ timeTitle }}</span>
      <br />
      по
      {{ dativeLoco }}
      {{ genetivePark }}
      на Восточном полигоне
    </div>
    <div v-else-if="tableType === 'rep_10'">
      Справка о простое
      {{ genetiveLoco }}
      в пунктах основных и оборотных депо<br />
      на <span class="page-subtitle-bold">{{ timeTitle }}</span> по
      {{ dativeRoad }}
    </div>
    <div v-else-if="tableType === 'rep_14'">
      {{ parsedTitleName }} по {{ dativeRoad.roadDislType }} для локомотивов
      {{ genetiveRoad.roadRegType }} c
      <span class="page-subtitle-bold">{{ timeTitle.split(' - ')[0] }}</span>
      по
      <span class="page-subtitle-bold">{{ timeTitle.split(' - ')[1] }}</span>
    </div>
    <div v-else-if="tableType === 'rep_15'">
      {{ parsedTitleName }} на
      <span class="page-subtitle-bold">{{ timeTitle }}</span>
    </div>
    <div v-else-if="tableType === 'rep_18'">
      {{ parsedTitleName }} на
      <span class="page-subtitle-bold">{{ timeTitle }}</span> на
      {{ prepositionalRoad }}
    </div>
    <div v-else-if="tableType.includes('rep_19_')">
      Отчет 19:
      {{ tableType === 'rep_19_2' || tableType === 'rep_19_5' ? ' сходы' : '' }}
      {{ parsedTitleName }}
      {{ roadType !== 0 ? ' (' + nominativeLoco + ')' : '' }}
      <span class="page-subtitle-bold">{{ timeTitle }}</span>
    </div>
    <div v-else-if="tableType === 'rep_20'">
      Справка о нахождении на МПР {{ genetiveLoco }} на
      {{ prepositionalRoad }} на
      <span class="page-subtitle-bold">{{ timeTitle }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'InfoHeader',
  props: [
    'timeTitle',
    'title',
    'locoType',
    'parkType',
    'tableType',
    'roadType'
  ],
  data() {
    return {
      nominative: {
        loco: [
          { id: 0, name: 'локомотивы' },
          { id: 1, name: 'электровозы' },
          { id: 2, name: 'тепловозы' }
        ]
      },
      dative: {
        loco: [
          { id: '0', name: 'локомотивам' },
          { id: '1,2,6,9', name: 'электровозам и тепловозам' },
          { id: '1,6', name: 'электровозам' },
          { id: '2,9', name: 'тепловозам' },
          {
            id: '3,4,5,7,8,10',
            name: 'локомотивам (кроме электровозов и тепловозов)'
          }
        ],
        road: [
          { id: 9991, name: 'Восточному полигону' },
          { id: 88, name: 'дирекции КРЖД' },
          { id: 92, name: 'дирекции ВСЖД' },
          { id: 94, name: 'дирекции ЗБЖД' },
          { id: 96, name: 'дирекции ДВЖД' }
        ]
      },
      genetive: {
        park: [
          { id: 0, name: 'эксплуатируемого парка' },
          { id: 1, name: 'неэксплуатируемого парка' },
          { id: -1, name: '' }
        ],
        loco: [
          { id: 0, name: 'локомотивов' },
          { id: 1, name: 'электровозов' },
          { id: 2, name: 'тепловозов' },
          { id: 3, name: 'электровозов и тепловозов' },
          { id: 4, name: 'локомотивов (кроме электровозов и тепловозов)' }
        ],
        road: [
          { id: 9991, name: 'Восточного полигона' },
          { id: 88, name: 'дирекции КРЖД' },
          { id: 92, name: 'дирекции ВСЖД' },
          { id: 94, name: 'дирекции ЗБЖД' },
          { id: 96, name: 'дирекции ДВЖД' }
        ]
      },
      prepositional: {
        road: [
          { id: 9991, name: 'Восточном полигоне' },
          { id: 1, name: 'Восточном полигоне' },
          { id: 88, name: 'Красноярской ЖД' },
          { id: 92, name: 'Восточно-Сибирской ЖД' },
          { id: 94, name: 'Забайкальской ЖД' },
          { id: 96, name: 'Дальневосточной ЖД' },
          { id: -1, name: 'Восточном полигоне' }
        ]
      }
    };
  },
  computed: {
    parsedTitleName() {
      let str = this.title.split(': ')[1];
      return !this.tableType.includes('rep_19_')
        ? str[0].toUpperCase() + str.slice(1)
        : str;
    },
    dativeLoco() {
      return this.dative.loco.find((item) => item.id === this.locoType).name;
    },
    genetivePark() {
      return this.genetive.park.find((item) => item.id === this.parkType).name;
    },
    genetiveLoco() {
      return this.genetive.loco.find((item) => item.id === this.locoType).name;
    },
    dativeRoad() {
      if (typeof this.roadType === 'object') {
        let dativeRoads = {};
        for (let key in this.roadType) {
          dativeRoads[key] = this.dative.road.find(
            (item) => item.id === this.roadType[key]
          ).name;
        }
        return dativeRoads;
      }
      return this.dative.road.find((item) => item.id === this.roadType).name;
    },
    genetiveRoad() {
      if (typeof this.roadType === 'object') {
        let genetiveRoads = {};
        for (let key in this.roadType) {
          genetiveRoads[key] = this.genetive.road.find(
            (item) => item.id === this.roadType[key]
          ).name;
        }
        return genetiveRoads;
      }
      return this.dative.road.find((item) => item.id === this.roadType).name;
    },
    prepositionalRoad() {
      return this.prepositional.road.find((item) => item.id === this.roadType)
        .name;
    },
    nominativeLoco() {
      return this.nominative.loco.find((item) => item.id === this.roadType)
        .name;
    }
  }
};
</script>

<style scoped></style>
